<template>
  <div class="shopcart">
    <van-nav-bar
      title="购物车"
      left-text="返回"
      :right-text="isbool?'编辑':'完成'"
      @click-right="manage"
      left-arrow
      @click-left="$router.go(-1)"
    />
    <div v-if="ShopCarData.length==0">
      <van-empty description="购物车空空如也，逛一逛" />
      <van-divider />
      <recomment></recomment>
    </div>
    <div v-else>
      <div class="list clearfix">
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          @load="onLoad"
          :offset="50"
        >
          <!-- <van-cell >      -->
          <!-- 滑块删除部分 -->
          <van-swipe-cell class="list-items" v-for="(item,index) in ShopCarData" :key="index">
            <div class="list-item clearfix">
              <div class="fl checkbox-item">
                <van-checkbox v-model="item.isCheck" shape="round" @click="oneSelect" />
              </div>
              <div class="fl product-info clearfix">
                <div class="product-img fl">
                  <img :src="item.small_img" alt class="img_auto" />
                </div>
                <div class="fl product-content">
                  <div class="product-text">
                    <div class="clearfix product-name">
                      <span class="product-title fl">{{item.name}}</span>
                      <span class="fl product-rule">{{item.rule}}</span>
                    </div>
                    <div class="product-enname">{{item.enname}}</div>
                  </div>

                  <div class="product-price">
                    <div class="price fl">￥{{item.price}}</div>
                    <div class="fr product-count">
                      <van-stepper
                        v-model="item.count"
                        theme="round"
                        button-size="22"
                        disable-input
                        @change="changeCarnum(item)"
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <template #right>
              <van-button square type="danger" text="删除" @click="removeOneCar([item.sid],index)" />
            </template>
          </van-swipe-cell>
        </van-list>
      </div>
    </div>
    <div v-if="isbool">
      <van-submit-bar
        class="submit-bar"
        :price="total"
        button-text="提交订单"
        @submit="goPay"
        :disabled="isdisable"
      >
        <van-checkbox v-model="allCheck" @click="AllSelect" :disabled='is'>全选</van-checkbox>
      </van-submit-bar>
    </div>
    <div v-else>
      <van-submit-bar
        class="submit-bar submit-bar-delete"
        button-text="删除选择"
        @submit="removeMuchCar"
        :disabled="isdisable"
      >
        <van-checkbox v-model="allCheck" @click="AllSelect">全选</van-checkbox>
      </van-submit-bar>
    </div>
  </div>
</template>
<script>
import recomment from "../../components/recomment.vue";
export default {
  data() {
    return {
      // 管理部分
      isbool: true,
      allCheck: false,
      count: 1,
      is:false,
      isdisable: true,
      // 当前页面展示的数量
      ShopCarData: [],
      // 购物车中真实的总数量
      allShopCarData: [],
      loading: true,
      finished: false,
      total: 0
    };
  },
  created() {
    this.$store.state.active = 2;
    this.getCardata();
    this.num();
  },
  methods: {
    //查询获取购物车商品数量
    getCardata() {
      let tokenString = localStorage.getItem("__jigng");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "get",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(res => {
          this.$toast.clear();

          
          if (res.data.code == 5000) {
            res.data.result.forEach(v => {
              v.isCheck = false;
            });
            this.allShopCarData = res.data.result;

            // 当前页面展示8个
            //数组截取直到截取完
            this.ShopCarData = this.allShopCarData.splice(0, 5);
            // 

            //根据所有的数据长度来判断是否被加载完成
            if (this.allShopCarData.length > 0) {
              //触发加载事件
              this.loading = false;
            } else {
              this.loading = true;
              this.Finished = true;
            }

            if (this.ShopCarData.length <= 5) {
              this.loading = false;
            }

            
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    // 列表加载
    onLoad() {
      
      setTimeout(() => {
        // 
        //全部添加到循环数组中
        this.ShopCarData.push(...this.allShopCarData.splice(0, 5));
        
        // 加载状态结束
        

        if (this.allShopCarData.length == 0) {
          
          //没有数据可加载
          this.loading = true;
          this.finished = true;
        }
      }, 1000);
    },
    // 全选部分
    AllSelect() {
      this.ShopCarData.forEach(v => {
        // 添加一个属性isCheck
        // 
        v.isCheck = this.allCheck;
      });
      // 
      this.isdisable = !this.allCheck;
      this.num();
    },
    // 单选部分
    oneSelect() {
      this.num();

      this.isdisable = true;
      for (let i = 0; i < this.ShopCarData.length; i++) {
        if (this.ShopCarData[i].isCheck) {
          this.isdisable = false;
          break;
        }
      }
      for (let j = 0; j < this.ShopCarData.length; j++) {
        if (!this.ShopCarData[j].isCheck) {
          //  this.isdisable=true
          this.allCheck = false;
          return;
        }
      }

      this.allCheck = true;
    },
    manage() {
      this.isbool = !this.isbool;
    },
    // 修改数量
    changeCarnum(item) {
      // 

      let tokenString = localStorage.getItem("__jigng");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "post",
        url: "/modifyShopcartCount",
        data: {
          appkey: this.appkey,
          tokenString,
          sid: item.sid,
          count: item.count
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == 6000) {
            this.num();
            // 
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 删除单个购物车
    removeOneCar(sids, index) {
      // 

      let tokenString = localStorage.getItem("__jigng");
      this.$toast.loading({
        message: "删除中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "post",
        url: "/removeShopcart",
        data: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(sids)
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == 7000) {
            this.ShopCarData.splice(index, 1);
            this.num();
            // 
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 多个删除
    removeMuchCar() {
      let tokenString = localStorage.getItem("__jigng");
      let removeSids = [];
      this.ShopCarData.forEach(v => {
        if (v.isCheck) {
          removeSids.push(v.sid);
        }
      });
      // 

      this.axios({
        method: "post",
        url: "/removeShopcart",
        data: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(removeSids)
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == 7000) {
            // 从后开始不会改变数组长度
            for (let i = this.ShopCarData.length - 1; i >= 0; i--) {
              if (this.ShopCarData[i].isCheck) this.ShopCarData.splice(i, 1);
            }
            this.num();
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 计算总价
    num() {
      this.total = 0;
      this.ShopCarData.forEach(v => {
        if (v.isCheck) {
          this.total += v.count * v.price * 100;
        }
      });
    },
    //跳转支付页面
    goPay() {

      if(this.ShopCarData.length == 0){
               return
            }
      let sidData = [];
      this.ShopCarData.forEach(v => {
        if (v.isCheck) {
          sidData.push(v.sid);

          // 
        }
      });

      this.$router.push({
        name: "支付",
        query: {
          sids: sidData.join("-")
        }
      });
    }
  },
  components: {
    recomment
  }
};
</script>

<style lang="less" scoped>
.van-nav-bar {
    position: fixed;
    top: 0;
    width: 100%;
}

.shopcart {
    margin-bottom: 40px;

    .list {
        background-color: #fff;
        margin-top: 50px;
        margin-bottom: 50px;
    }

    .list-item {
        padding: 20px;
    }

    .checkbox-item {
        width: 20px;
        height: 20px;
        margin-top: 30px;
        margin-left: 5px;
    }

    .list-items:not(:last-child) {
        border-bottom: 1px solid #e8e8e8;
    }

    /deep/ .list-items .van-button {
        height: 100%;
    }

    .product-info {
        width: calc(~"100% - 35px");
        margin-left: 10px;
    }

    .product-img {
        width: 80px;
        height: 80px;

        img {
            border-radius: 10px;
        }
    }

    .product-content {
        width: calc(~"100% - 100px");
        height: 80px;
        margin-left: 20px;
    }

    .product-title {
        margin-right: 10px;
        font-size: 14px;
        color: black;
    }

    .product-rule {
        font-size: 12px;
        color: #666;
        line-height: 24px;
        margin-left: 30px;
    }

    .product-enname {
        font-size: 10px;
        color: #666;
        height: 20px;
    }

    .product-name {
        height: 30px;
    }

    .product-text {
        height: 50px;
    }

    .product-price {
        height: 30px;
    }

    .price {
        font-size: 14px;
        line-height: 30px;
    }

    .product-count {
        margin-top: 4px;
    }

    /deep/ .submit-bar {
        bottom: 50px;
        padding-left: 9px;
    }

    /deep/ .submit-bar-delete .van-button {
        margin-left: auto;
    }
}
</style>
